.media {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: var(--font2);

  &:hover {
    background-color: #fff;
    color: #0b0b0b;
  }

  .poster {
    border-radius: 10px;
  }

  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
  }
  .subTitle {
    display: flex;
    justify-content: space-between;
    padding: 0 2px;
    font-weight: 200;
    padding-bottom: 3px;
  }
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}
